import React, { useState, useEffect } from 'react'
import { useHistory, useLocation } from 'react-router-dom'
import { ExclamationCircleOutlined } from '@ant-design/icons'
import { Modal, notification, Pagination } from 'antd'
import { Editor } from "react-draft-wysiwyg";
import { EditorState, convertToRaw } from 'draft-js';
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
import draftToHtml from 'draftjs-to-html';

import {
  CForm,
  CFormText,
  CCardFooter,
  CInputFile,
  CCard,
  CCardBody,
  CCardHeader,
  CAlert,
  CCol,
  CDataTable,
  CBadge,
  CFormGroup,
  CLabel,
  CRow,
  CInput,
  CTextarea,
  CButton,
  CPagination
} from '@coreui/react'
import config from '../../config'
import axios from 'axios'

const getBadge = status => {
  switch (status) {
    case 'COMPLETED':
    return 'success'
    case 'FAILED':
    return 'secondary'
    case 'Pending':
    return 'warning'
    case 'FAILED':
    return 'danger'
    default:
    return 'primary'
  }
}

const Blog = () => {
  const history = useHistory()
  const [exchangeData, setExchangeData] = useState([])
  const [success, setSuccess] = useState(false)
  const [error, setError] = useState(false)
  const [viewContentModal, setViewContentModal] = useState(false)
  const [viewBidModal, setViewBidModal] = useState(false)
  const[title, setTitle] = useState("")
  const [subtitle, setSubtitle] = useState("")
  const [readTime, setReadTime] = useState("")
  const [createBlogModal, setCreateBlogModal] = useState(false)
  const [fill, setFill] = useState(false)
  const [msg, setMsg] = useState("")
  const [editorState, setEditorState] = useState(EditorState.createEmpty())
  const [imageUrlA, setImageUrlA] = useState("")
  const [imageUrlB, setImageUrlB] = useState("")
  const [blogContent, setBlogContent] = useState("")
  const [blogImages, setBlogImages] = useState([])






  const [page, setPage] = useState(1)
  const [totalItems, setTotalItems] =  useState("")

  const [askInfo, setAskInfo] = useState({})
  const [bidInfo, setBidInfo] = useState({})
  const [load, setLoad] = useState(false)
  const [reason, setReason] = useState('')
  const [userId, setUserId] = useState('')
  const [loadView, setLoadView] = useState(false)


  const  onEditorStateChange = (editorState) => {
    console.log(editorState)
    setEditorState(editorState)
  };

  const closeContentModal = () => {
    setViewContentModal(false)
  }





  const closeViewAsk = () => {
    setViewContentModal(false)
  }

  const closeViewBid = () => {
    setViewBidModal(false)
  }

  const getPaged = queryString => {
    axios.get(`${config.baseUrl}/api/v1/admin/transactions/exchange?${queryString}`, {
      headers: {
        Authorization: `Bearer ${sessionStorage.getItem('token')}`
      }
    })
    .then(res => {
      if (res.status === 200) {
        setTotalItems(res.data.totalPages * 10)
        setExchangeData(
          res.data.records.map(data => ({
            transactionStatus: data.transactionStatus,
            reference: data.reference,
            exchangeStatus: data.exchangeStatus,
            createdOn: data.createdOn ? data.createdOn.slice(0,10) : "",
            ask: data.ask,
            bid:data.bid
          }))
          )
      }
    })
    .catch(err => {
      if (err) {
      }
    })
  }

  const pagination = (page, pageSize) => {
    console.log(page)
    setPage(page)
    const queryString = `pageNumber=${page}&pageSize=10&startDate&endDate&exhangeStatus&transactionStatus&reference`
    getPaged(queryString)
  }

  useEffect(() => {
    getBlogs()
  }, [])

  const getBlogs= () => {
    axios
    .get(
      `${config.baseUrl}/api/v1/admin/blogs?pageSize=10&pageNumber=1&blogStatus=&title=&startDate=&endDate=`,
      {
        headers: {
          Authorization: `Bearer ${sessionStorage.getItem('token')}`
        }
      }
      )
    .then(res => {
      if (res.status === 200) {
        setTotalItems(res.data.totalPages * 10)
        setExchangeData(
          res.data.content.map(data => ({
            title: data.title,
            id:data.id,
            subTitle: data.subTitle,
            readTime: data.readTime,
            createdOn: data.createdOn ? data.createdOn.slice(0,10) : "",
            status:data.blogStatus,
            content:data.content,
            imagesBlog: data.imageUrls
          }))
          )
      }
    })
    .catch(err => {
      if (err) {
      }
    })
  }

  const postBlog = () => {
    if (
      title &&
      subtitle &&
      editorState &&
      readTime 
      ) {
      setLoad(true)
    axios
    .post(
      `${config.baseUrl}/api/v1/admin/blogs
      `,
      {
        "title" : title,
        "subTitle" : subtitle,
        "readTime" :readTime,
        "content" :draftToHtml(convertToRaw(editorState.getCurrentContent())),
        "imageUrls" : [
        imageUrlA, imageUrlB
        ]
      },
      {
        headers: {
          Authorization: `Bearer ${sessionStorage.getItem('token')}`
        }
      }
      )
    .then(res => {
      setLoad(false)
      if (res.status === 200) {
        setSuccess(true)
        setCreateBlogModal(false)
        setTitle("")
        setSubtitle("")
        setImageUrlA("")
        setImageUrlB("")
        setMsg('Blog Post Created Successfully')
        setTimeout(() => {
          setMsg('')
          setCreateBlogModal(false)
          setSuccess(false)
        }, 3000)
        getBlogs()
      }
    })
    .catch(err => {
      setLoad(false)
      if (err.response !== undefined) {
        setMsg(err.response.data.message)
        setError(true)
        setSuccess(false)
        setTimeout(() => {
          setMsg('')
          setError(false)
        }, 2500)
      } else {
        setMsg('Connection Error')
        setError(true)
        setSuccess(false)
      }
    })
  } else {
      // setError(true)
      // setMsg("All fie")
      setFill(true)
      setTimeout(() => {
        setFill(false)
      }, 2500)
    }
  }

  const closeCreateBlog = () => {
    setCreateBlogModal(false)
    setTitle("")
    setSubtitle("")
    setImageUrlA("")
    setImageUrlB("")
  }

  const viewContentFunc = (id, e) => {
    setBlogContent(id.content)
    setBlogImages(id.imagesBlog)
    if(id.content){
      setViewContentModal(true)

    }
    
  }

  const viewBidFunc = (id, e) => {
    setBidInfo(id.bid)
    setViewBidModal(true)
    
  }

  const Notification = (type, msgType, msg) => {
    notification[type]({
      message: msgType,
      description: msg
    })
  }

  const createBlog = () => {
    setCreateBlogModal(true)
  }

  const changeStatusConfirmActivate = (id) => {
    console.log(id)
    Modal.confirm({
      title: `Are you sure you want to ${id.status === "INACTIVE" ? "Activate" :"Deactivate"}?`,
      icon: <ExclamationCircleOutlined />,
      okText: 'Yes',
      okType: 'danger',
      cancelText: 'No',
      onOk () {

        if(id.status === "INACTIVE"){
          activate(id.id)
        }
        else{
          deActivate(id.id)
        }
      },
      onCancel () {
        console.log('Cancel')
      }
    })
  }

    const changeStatusConfirmDelete = (id, e) => {
    e.preventDefault()
    Modal.confirm({
      title: `Are you sure you want to delete this blog?`,
      icon: <ExclamationCircleOutlined />,
      okText: 'Yes',
      okType: 'danger',
      cancelText: 'No',
      onOk () {

          deleteBlog(id.id)
      
      },
      onCancel () {
        console.log('Cancel')
      }
    })
  }

  const activate = (id) => {
    console.log(id)
    setLoad(true)
    axios
    .put(`${config.baseUrl}/api/v1/admin/blogs/${id}/activate`,{}, {
      headers: {
        Authorization: `Bearer ${sessionStorage.getItem('token')}`
      }
    })
    .then(res => {
      setLoad(false)
      if (res.status === 200) {
        setSuccess(true)
        setMsg('Blog Activated Successfully')
        getBlogs()
        setError(false)
        setTimeout(()=>{
          setSuccess(false)
        },2000)
      }
    })
    .catch(err => {
      setLoad(false)
      setSuccess(false)
      if (err.response !== undefined) {
        setMsg(err.response.data.message)
        setError(true)
        setTimeout(() => {
          setError(false)
        }, 2500)
      } else {
        setMsg('Connection Error')
        setError(true)
        setTimeout(() => {
          setError(false)
        }, 2500)
      }
    })
  }

  const deleteBlog = (id, e) => {
    console.log(id)
    setLoad(true)
    axios
    .delete(`${config.baseUrl}/api/v1/admin/blogs/${id}`, {
      headers: {
        Authorization: `Bearer ${sessionStorage.getItem('token')}`
      }
    })
    .then(res => {
      setLoad(false)
      if (res.status === 200) {
        setSuccess(true)
        setMsg('Blog Deleted Successfully')
        getBlogs()
        setError(false)
        setTimeout(()=>{
          setSuccess(false)
        },2000)
      }
    })
    .catch(err => {
      setLoad(false)
      setSuccess(false)
      if (err.response !== undefined) {
        setMsg(err.response.data.message)
        setError(true)
        setTimeout(() => {
          setError(false)
        }, 2500)
      } else {
        setMsg('Connection Error')
        setError(true)
        setTimeout(() => {
          setError(false)
        }, 2500)
      }
    })
  }

  const deActivate = (id) => {
    console.log(id)
    setLoad(true)
    axios
    .put(`${config.baseUrl}/api/v1/admin/blogs/${id}/de-activate`,{}, {
      headers: {
        Authorization: `Bearer ${sessionStorage.getItem('token')}`
      }
    })
    .then(res => {
      setLoad(false)
      if (res.status === 200) {
        setSuccess(true)
        setMsg('Blog Activated Successfully')
        getBlogs()
        setError(false)
        setTimeout(()=>{
          setSuccess(false)
        },2000)
      }
    })
    .catch(err => {
      setLoad(false)
      setSuccess(false)
      if (err.response !== undefined) {
        setMsg(err.response.data.message)
        setError(true)
        setTimeout(() => {
          setError(false)
        }, 2500)
      } else {
        setMsg('Connection Error')
        setError(true)
        setTimeout(() => {
          setError(false)
        }, 2500)
      }
    })
  }

  return (
    <CRow>
    <CCol>
    {success && <CAlert color='success'>{msg}</CAlert>}

        {error && <CAlert color='danger'>{msg}</CAlert>}
    <CCard>
    <CCardHeader>
    <div className='d-flex justify-content-between align-items-center'>
    <div>Blog Posts </div>
    <button
    type='button'
    class='btn btn-primary mr-2'
    onClick={createBlog}
    >
    Create BlogPost
    </button>
    </div>
    </CCardHeader>          <CCardBody>
    <CDataTable
    items={exchangeData}
    fields={[
      {
        key: 'title',
        name: 'Title',

      },
      {
        key: 'subTitle',
        name: 'Subtitle',

      },

      {
        key: 'readTime',
        name: 'Read Time',

      },
      {
        key: 'Content',
        name: 'Content & Images',

      },

      {
        key: 'status',
        name: 'Status',

      },



      {
        key: 'Actions',
        name: 'Actions',

      }
      ]}


      scopedSlots={{
        Actions: item => (
          <td className='d-flex'>
          <button
          type='button'
          class='btn btn-info mr-2'
          onClick={changeStatusConfirmActivate.bind(this, item)}
          >
          {item.status === "INACTIVE" ? "ACTIVATE" : "DEACTIVATE"}
          </button>
          <button
          type='button'
                    onClick={changeStatusConfirmDelete.bind(this, item)}
          class='btn btn-danger'
          >
          Delete
          </button>


          </td>
          ),


        Content: item => (
          <td className='d-flex'>
          <button
          type='button'
          class='btn btn-info mr-2'
          onClick={viewContentFunc.bind(this, item)}
          >
          View Content
          </button>



          </td>
          ),





      }}
      />
      <div className='text-center pagination-part'>
      <Pagination
      current={page}
      total={totalItems}
      showTotal={(total, range) => `${range[0]}-${range[1]} of ${total} items`}
      defaultPageSize={10}
      onChange={pagination}
      />
      </div>

      </CCardBody>
      </CCard>
      </CCol>


      <Modal
            // title={
            //   updateState ? 'Update Limit Profile' : 'Create Limit Profile'
            // }
            width={1200}
            visible={createBlogModal}
            footer={null}
            maskClosable={false}
            onCancel={closeCreateBlog}
            >
            <div className='container'>
            {fill && (
              <p className='text-danger text-center'>
              All fields are required{' '}
              </p>
              )}
            <form>
            {success && <CAlert color='success'>{msg}</CAlert>}

            {error && <CAlert color='danger'>{msg}</CAlert>}
            <>
            <CFormGroup>
            <CLabel htmlFor='name'>Blog Title</CLabel>
            <CInput
            id='name'
            type='text'
            required
            onChange={e => setTitle(e.target.value)}
            value={title}
            />
            </CFormGroup>
            <CFormGroup>
            <CLabel htmlFor='name'>Blog Subtitle</CLabel>
            <CInput
            id='name'
            type='text'
            required
            onChange={e => setSubtitle(e.target.value)}
            value={subtitle}
            />
            </CFormGroup>
            <CFormGroup>
            <CLabel htmlFor='name'>Read Time</CLabel>
            <CInput
            id='text'
            type='number'
            required
            onChange={e => setReadTime(e.target.value)}
            value={readTime}
            />
            </CFormGroup>
            <br />
            <CLabel htmlFor='name'>Blog Content</CLabel>

            <Editor
            editorState={editorState}
            toolbarClassName="toolbarClassName"
            wrapperClassName={{border: "2px solid blue", padding: "0"}}
            editorClassName={{border: "2px solid blue", padding: "0"}}
            onEditorStateChange={onEditorStateChange}
            />

            <br />
            <br />

            <CFormGroup>
            <CLabel htmlFor='name'>Image URL 1</CLabel>
            <CInput
            id='name'
            type='text'
            onChange={e => setImageUrlA(e.target.value)}
            value={imageUrlA}
            />
            </CFormGroup>

            <CFormGroup>
            <CLabel htmlFor='name'>Image URL 2</CLabel>
            <CInput
            id='name'
            type='text'
            onChange={e => setImageUrlB(e.target.value)}
            value={imageUrlB}
            />
            </CFormGroup>


{/*            <p>{draftToHtml(convertToRaw(editorState.getCurrentContent()))}</p>
*/}





</>

<br />
<div className='d-flex justify-content-end'>
<button
type='button'
class='btn btn-primary mr-2'
onClick={postBlog}
>
{load ? (
  <div
  class='spinner-border'
  role='status'
  style={{ width: '1rem', height: '1rem' }}
  >
  <span class='sr-only'>Loading...</span>
  </div>
  ) : (
  'Post'
  )}
  </button>
  </div>
  </form>
  </div>
  </Modal>



  <Modal
  title={'View Content'}
  visible={viewContentModal}
  footer={null}
  maskClosable={false}
  onCancel={closeContentModal}
  width={1000}
  >
  <div className='container'>
  <CRow>
  <CCol xs='12' md='12' lg="12">
  <CCard style={{padding: "30px"}}>
  {blogContent}

  </CCard>
  {blogImages.length > 0 &&
    <CCard style={{padding: "30px"}}>
    {blogImages && blogImages.map((img) => {
      return(
        <img src={img} alt="" style={{width: "100%", height: "400px"}} className="mb-5" />
        )
    })}

    </CCard>}
    </CCol>
    </CRow>
    </div>
    </Modal>


    </CRow>
    )
}

export default Blog
